:root {
  --font-family: "Open Sans", sans-serif;
  --content-width: 1216px;
  --container-offset: 23px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --black-color: #000;
  --text-color: #060606;
  --primary-green: #1baa7d;
  --neutral-dark: #494949;
  --neutral-lightest: #f5f5f5;
  --neutral-darkest: #303030;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  border-radius: 20px;
  padding: 12px 20px;
}
.btn--primary {
  color: var(--light-color);
  background: var(--primary-green);
}
.btn--primary:hover {
  color: var(--primary-green);
  background: var(--light-color);
  border: 1px solid var(--primary-green);
}
.btn--secondary {
  color: var(--black-color);
  background: var(--light-color);
}
.btn--secondary:hover {
  background-color: var(--primary-green);
  color: var(--light-color);
  border: 1px solid var(--light-color);
}

.section__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 44px;
  text-align: center;
  color: var(--neutral-darkest);
}

.swiper {
  width: 100%;
  height: 100%;
}

.header {
  width: 100%;
  height: 75px;
  border-bottom: 1px solid #d6d6d6;
}
.header__container {
  height: 100%;
}
.header__inner {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__list {
  display: flex;
  align-items: center;
  gap: 43px;
}
.header__list_item a {
  font-family: "Zona Pro", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
  color: #202020;
}
.header__list_item a:hover {
  color: var(--primary-green);
}
.header__btn {
  font-weight: 600;
  font-size: 16px;
  color: #fff;
}

/*big components:*/
.successes__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 32px;
  text-align: center;
  color: #303030;
}
.successes__inner {
  padding-top: 60px;
  width: 100%;
}
.successes__header {
  display: flex;
  justify-content: space-between;
}
.successes__results {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.successes__footer {
  padding-top: 30px;
}

.selector {
  background: var(--light-color);
  overflow: hidden;
  width: 168px;
  min-height: 44px;
  max-height: 44px;
  border: 1px solid #303030;
  border-radius: 24px;
  transition: all 1.5s ease;
}
.selector__header {
  padding: 12px 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.selector__header span {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 14px;
  line-height: 143%;
  color: #303030;
}
.selector__btn svg {
  transition: all 1s ease;
}
.selector__body {
  width: 100%;
  background: var(--light-color);
  display: flex;
  align-items: start;
  justify-content: start;
  padding: 12px 15px;
  border-top: 1px solid #303030;
}
.selector.active {
  transition: all 1.5s ease;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  max-height: 150px;
}
.selector.active .selector__body {
  padding: 12px 15px;
  border-top: 1px solid #303030;
  border-radius: 0 0 24px 24px;
}
.selector.active .selector__btn svg {
  transform: rotate(180deg);
}

.chart {
  border-radius: 32px;
  padding: 32px 32px 40px 32px;
  width: 100%;
  height: 357px;
  background-color: var(--neutral-lightest);
}
.chart__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.chart__body {
  padding-bottom: 40px;
  height: 100%;
  display: flex;
  align-items: end;
  gap: 20px;
}
.chart__title {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  text-align: center;
  color: var(--neutral-darkest);
}
.chart__navigation {
  display: flex;
  align-items: center;
  gap: 12px;
}
.chart__navigation_btn {
  border: 1px solid #303030;
  border-radius: 50px;
  padding: 8px;
  width: 36px;
  height: 36px;
}
.chart__points {
  padding-bottom: 50px;
}
.chart__points_item {
  text-align: right;
  margin-top: 32px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #7d7979;
}
.chart__lines {
  display: flex;
  align-items: center;
  gap: 20px;
}
.chart__line {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: end;
  height: 200px;
}
.chart__line_title {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  text-align: center;
  color: #7d7979;
}
.chart__line_candle {
  border-radius: 12px;
  width: 44px;
  max-height: 200px;
  background-color: var(--primary-green);
}

.results-card {
  border-radius: 32px;
  padding: 32px;
  width: 384px;
  height: 212px;
  background-color: var(--neutral-lightest);
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 24px;
}
.results-card__title {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  text-align: center;
  color: #303030;
}
.results-card__info {
  display: flex;
  align-items: center;
  gap: 10px;
}
.results-card__info span {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 44px;
  text-align: center;
  color: #303030;
}
.results-card__percent {
  display: flex;
  align-items: center;
  gap: 5px;
  border-radius: 28px;
  padding: 4px 12px 4px 8px;
  width: 97px;
  height: 36px;
  background-color: #ebfaf3;
}
.results-card__percent span {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 175%;
  text-align: center;
  color: #1baa7d;
}
.results-card__summary {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  text-align: center;
  color: #7d7979;
}

.continue-subject {
  margin-bottom: 35px;
  border-radius: 32px;
  padding: 28px 32px;
  width: 100%;
  height: 236px;
  background-color: var(--neutral-lightest);
}
.continue-subject__module {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 12px;
  padding: 8px 12px;
  width: 105px;
  height: 44px;
  background-color: #ebfaf3;
}
.continue-subject__module span {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
  color: var(--neutral-darkest);
}
.continue-subject__header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.continue-subject__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  color: var(--neutral-darkest);
}
.continue__subjects {
  padding-top: 50px;
}
.continue__inner {
  padding-top: 50px;
  padding-bottom: 30px;
}
.continue__footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.continue__link {
  text-align: center;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 14px;
  line-height: 143%;
  color: var(--primary-green);
}

.subject-progress {
  padding-top: 30px;
}
.subject-progress__list {
  display: flex;
  align-items: start;
  justify-content: space-between;
}
.subject-progress__bar {
  position: relative;
  margin-top: 30px;
  width: 100%;
  border-radius: 8px;
  height: 12px;
  background-color: #d6d6d6;
}
.subject-progress__bar::before {
  content: "";
  position: absolute;
  z-index: 10;
  left: 0;
  top: 0;
  height: 100%;
  width: 27%;
  border-radius: 8px;
  background-color: #1baa7d;
}
.subject-progress__bar::after {
  content: "";
  position: absolute;
  z-index: 9;
  left: 0;
  top: 0;
  height: 100%;
  width: 47%;
  border-radius: 8px;
  background-color: #f06c20;
}

.progress-item {
  display: flex;
  align-items: center;
  border: 2px solid #7d7979;
  border-radius: 20px;
  padding: 16px 20px;
  width: 354px;
  height: 60px;
}
.progress-item__icon {
  margin-right: 15px;
}
.progress-item__title {
  margin-right: 90px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
  color: var(--neutral-darkest);
}
.progress-item__count {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  color: var(--neutral-darkest);
}

.top-classmates__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
}
.top-classmates__tabs {
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  border-radius: 28px;
  width: 344px;
  height: 52px;
  background-color: var(--neutral-lightest);
}
.top-classmates__tab {
  cursor: pointer;
  text-align: center;
  padding: 12px 0px;
  width: 50%;
  height: 100%;
}
.top-classmates__tab span {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 175%;
  color: var(--neutral-darkest);
}
.top-classmates__tab.active {
  border-radius: 28px;
  background-color: var(--neutral-darkest);
}
.top-classmates__tab.active span {
  color: var(--light-color);
}

.classmates-table {
  margin-top: 40px;
  width: 100%;
}
.classmates-table__header th {
  text-align: left;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
  color: #7d7979;
}

.table-item {
  position: relative;
  height: 80px;
}
.table-item__place {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: start;
}
.table-item__place div {
  width: 32px;
  height: 32px;
  position: relative;
}
.table-item__place svg {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.table-item__place span {
  position: absolute;
  z-index: 11;
  left: 35%;
  top: 50%;
  transform: translateY(-50%);
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
}
.table-item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  z-index: 100;
  background-color: #d6d6d6;
}

.table-item__points,
.table-item__done,
.table-item__name,
.table-item__time {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
  color: var(--neutral-darkest);
}

.table-item__points {
  color: var(--primary-green);
}

.other-subjects__inner {
  padding-top: 40px;
  padding-bottom: 100px;
}
.other-subjects__list {
  width: 100%;
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
}
.other-subjects__item {
  flex-grow: 1;
}
.other-subjects__top {
  width: 100%;
  padding-top: 50px;
}
.other-subjects__bottom {
  padding-top: 30px;
  width: 100%;
}

/* small components:*/
.subjects-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 32px;
  padding: 28px 32px;
  min-width: 384px;
  height: 100px;
  background-color: var(--neutral-lightest);
}
.subjects-item h6 {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  color: var(--neutral-darkest);
}
.subjects-item span {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
  color: var(--neutral-darkest);
  border-radius: 12px;
  padding: 8px 12px;
  min-width: 121px;
  height: 44px;
  background-color: #ebfaf3;
}

.footer {
  width: 100%;
  height: 512px;
  background-color: #494949;
}
.footer__logo {
  width: 188px;
  height: 28px;
}
.footer__logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.footer__inner {
  padding-top: 56px;
}
.footer__main {
  display: flex;
  justify-content: space-between;
  padding-bottom: 60px;
}
.footer__main_left {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.footer__cols {
  display: flex;
  align-items: start;
  gap: 82px;
}
.footer__col {
  display: flex;
  flex-direction: column;
  align-items: start;
  text-align: left;
  gap: 28px;
}
.footer__col_title {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  color: var(--light-color);
}
.footer__col_list {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 12px;
}
.footer__col_item a {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 12px;
  line-height: 167%;
  color: #d6d6d6;
}
.footer__bottom {
  border-top: 1px solid #7d7979;
  padding-top: 20px;
}
.footer__bottom_copyright {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 12px;
  line-height: 167%;
  color: #d6d6d6;
}
.footer__bottom_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__agreement {
  display: flex;
  align-items: center;
  gap: 20px;
}
.footer__agreement_item a {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 14px;
  line-height: 143%;
  color: #d6d6d6;
}
.footer__address {
  max-width: 311px;
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 10px;
}
.footer__address a {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 14px;
  line-height: 167%;
  color: #d6d6d6;
}
.footer__socials_list {
  padding-top: 10px;
  display: flex;
  align-items: start;
  gap: 15px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}